export function getIdKeyForGroupBy(groupBy = {}) {
    if (groupBy.account) {
        return 'account';
    }
    if (groupBy.instance_type) {
        return 'instance_type';
    }
    if (groupBy.org_unit_id) {
        return 'org_unit_id';
    }
    if (groupBy.region) {
        return 'region';
    }
    if (groupBy.service) {
        return 'service';
    }
    return 'date';
}
